import './App.css';
import { useRecoilValue, useSetRecoilState } from 'recoil';
import { DarkModeAtom } from './common/atoms';
import styled, { ThemeProvider } from 'styled-components';
import { GlobalStyle } from './common/GlobalStyle';
import { Outlet } from 'react-router-dom';
import { darkTheme, lightTheme } from './theme/theme';
import { ReactQueryDevtools } from "react-query/devtools";
import CupertinoToggle from './common/cupertino_toggle';

const Container = styled.div`
    display: flex;
    padding: 0px 20px;
    max-width: 400px;
    margin: 0 auto;
    align-items: center;
`;
const ThemeText = styled.p`
  font-size: 8px;
  margin-top: 10px;
  margin-left: 4px;
  color: ${props => props.theme.textColor}
`;

function App() {
    const toggleDarkmode = () => {
      setDarkMode(current => !current);
    }
    const setDarkMode = useSetRecoilState(DarkModeAtom);
    const isDark = useRecoilValue(DarkModeAtom);
  
    return <>
      <ThemeProvider theme={isDark ? darkTheme : lightTheme}>
        <GlobalStyle />
        <Container>
          <CupertinoToggle />
          <ThemeText>{isDark ? "dark mode" : "light mode"}</ThemeText>
        </Container>
        <Outlet />
        <ReactQueryDevtools />
      </ThemeProvider>
    </>;
  
}

export default App;
