import { useOutletContext } from "react-router-dom";
import { useRecoilValue } from "recoil";
import { DarkModeAtom } from "../common/atoms";
import { CoinContext } from "../api/coins-api";
import { useQuery } from "react-query";
import { DefaultTheme, styled } from "styled-components";

const Container = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
`;
const CurrPrice = styled.h1`
    padding: 30px;
    font-size: 40px;
`;

const getTextColor = (value: number) => {
    const color = value > 0 ? "rgba(255, 0, 0, 1)" : "rgba(0, 0, 255, 1)";
    console.log("color: ", color);
    return color;
}

const Price = () => {
    const { coinId, tickerData } = useOutletContext<CoinContext>();
    const isDark = useRecoilValue(DarkModeAtom);

    return <Container>
        <CurrPrice style={{color: getTextColor(tickerData.quotes.USD.market_cap_change_24h)}}>{tickerData.quotes.USD.price.toFixed(2)}</CurrPrice>
        <h1 style={{color: getTextColor(tickerData.quotes.USD.market_cap_change_24h)}}>{tickerData.quotes.USD.market_cap_change_24h}%</h1>
    </Container>;
}

export default Price;