import { useState } from "react";
import { useQuery } from "react-query";
import { Link, Outlet, useLocation, useMatch, useParams } from "react-router-dom";
import { Container, Description, Header, Loading, Overview, OverviewItem, Tab, Tabs } from "./styled/coin-styled";
import { TickerData, fetchCoinInfo, fetchTickersData } from "../api/coins-api";

const Coin = () => {
    
    const [tabIndex, setTabIndex] = useState(0);
    const { coinId } = useParams();
    const { state } = useLocation();
    const { isLoading: isInfoLoading, data: infoData } = useQuery(["info",
        coinId],
        () => fetchCoinInfo(`${coinId}`));
    const { isLoading: isTickerLoading, data: tickersData }
        = useQuery<TickerData>(["tickers", coinId],
            () => fetchTickersData(`${coinId}`)    );
    
    const onTab = (index: number) => {
        setTabIndex(index);
    }

    const loading = isInfoLoading || isTickerLoading;

    return <>
        <Container>
            <Header>
                <h1>{!loading ? <img src={`${infoData?.logo}`} /> : null} {`${state?.name || "Loading"}`}</h1>
            </Header>
            {loading ? <Loading>loading....</Loading> :
                <>
                    <Overview>
                        <OverviewItem>
                            <h1>RANK:</h1>
                            <h1>{infoData?.rank}</h1>
                        </OverviewItem>
                        <OverviewItem>
                            <h1>SYMBOL:</h1>
                            <h1>{infoData?.symbol}</h1>
                        </OverviewItem>
                        <OverviewItem>
                            <h1>PRICE:</h1>
                            <h1>${tickersData?.quotes.USD.price.toFixed(2)}</h1>
                        </OverviewItem>
                    </Overview>

                    <Description>
                        {`${infoData?.description}`}
                    </Description>

                    <Overview>
                        <OverviewItem>
                            <h1>TOTAL SUPPLY:</h1>
                            <h1>{tickersData?.total_supply}</h1>
                        </OverviewItem>
                        <OverviewItem>
                            <h1>MAX SUPPLY:</h1>
                            <h1>{tickersData?.max_supply}</h1>
                        </OverviewItem>
                    </Overview>
                </>}
            <Tabs>
                <Tab selected={tabIndex == 0} >
                    <Link to="./chart"
                        onClick={() => setTabIndex(0)}>Chart</Link>
                </Tab>
                <Tab selected={tabIndex == 1} >
                    <Link
                        to={{
                            pathname: "./price",
                        }}
                        // to="./price"
                        onClick={() => setTabIndex(1)}>Price</Link>
                </Tab>
            </Tabs>
            <Outlet context={{
                coinId: coinId,
                tickerData: tickersData
            }} />
        </Container>
    </>;
}

export default Coin;