import { useOutletContext } from "react-router-dom";
import { useRecoilValue } from "recoil";
import { DefaultTheme, withTheme } from "styled-components";
import { DarkModeAtom } from "../common/atoms";
import { useQuery } from "react-query";
import { CoinContext, HistoryData, fetchPriceHistory } from "../api/coins-api";
import ApexChart from "react-apexcharts";


const Chart = (props: DefaultTheme) => {
    const { coinId } = useOutletContext<CoinContext>();
    const isDark = useRecoilValue(DarkModeAtom);

    const { isLoading, data } = useQuery<HistoryData[]>(["price", coinId], () => fetchPriceHistory(coinId));
    
    return <>
        {isLoading ? <h1>Loading</h1> :
            // makeLineChart(isDark, props, data!)
            makeCandleChart(isDark, props, data!)
        }
    </>;
    
}

const makeCandleChart = (isDark: boolean, props: DefaultTheme, data: HistoryData[]) => {
    return <ApexChart
        type="candlestick"
        options={{
            chart: {
                type: 'candlestick',
                width: 500,
                height: 200,
                background: props.theme.bgColor,
            },
            plotOptions: {
                candlestick: {
                  colors: {
                    upward: '#3C90EB',
                    downward: '#ff0000'
                  },
                  wick: {
                    useFillColor: true,
                  }
                            }
            },
            
            
        }}

        series={[{
            data: 
                data.map(v => [v.time_open, v.open, v.high, v.low, v.close])
          }]}

    />;
}

const makeLineChart = (isDark: boolean, props: DefaultTheme, data: HistoryData[]) => {
    return <ApexChart
        options={{
            theme: {
                mode: isDark ? "dark" : "light",
            },
            chart: {
                width: 500,
                height: 200,
                toolbar: {
                    show: false,
                },
                background: props.theme.bgColor,
            },
            dataLabels: {
                enabled: false,
            },
            grid: {
                show: false,
            },
            stroke: {
                curve: 'smooth',
                width: 2,
            },
            xaxis: {
                labels: {
                    show: false,
                },
                axisBorder: {
                    show: false,
                },
                axisTicks: {
                    show: false,
                }
            },
            yaxis: {
                labels: {
                    show: false,
                },

            }
        }}
        
        series={[
            {
                name: "price",
                data: data?.map((v) => v.close) || [],
            },
        ]} />;

}

export default withTheme(Chart);