import { Link, useMatch } from "react-router-dom";
import { Coin, CoinList, CoinModel, Container, Header, Loading, Title } from "./styled/coins-styled";
import { useQuery } from "react-query";
import { fetchCoins } from "../api/coins-api";

const Coins = () => {
    const { isLoading, data } = useQuery<CoinModel[]>({ queryKey: "", queryFn: fetchCoins });
    return <Container>
        <Header>
            <Title>코인</Title>
        </Header>
      
        {isLoading ? <Loading>Loading ...</Loading> : <CoinList>
            {data?.slice(0, 100).map(coin => <Coin key={`${coin.id}`}>
              <Link to={`/coin/${coin.id}`} state={{name: coin.name}}>
                {<img src={`https://coinicons-api.vercel.app/api/icon/${coin.symbol.toLowerCase()}`} />}
                {`${coin.name}`} &rarr;</Link>
            </Coin>)}
        </CoinList>}
    </Container>
}

export default Coins;